// this function is used to get the date without timezone
export const getDateWithoutTimezone = (date: Date | string) =>
  new Date(new Date(date).toISOString().slice(0, -1));

// this function is used to get the date with fixed timezone
export const getISODateStringWithFixedTimezone = (date: Date | string) => {
  const _date = new Date(date);
  const userTimezoneOffset = _date.getTimezoneOffset() * 60000;
  return new Date(_date.getTime() - userTimezoneOffset)
    .toISOString()
    .slice(0, -1);
};
